import { __ } from "@wordpress/i18n";
import { addFilter } from "@wordpress/hooks";
import { createHigherOrderComponent } from "@wordpress/compose";
import { Fragment } from "@wordpress/element";
import { InspectorControls } from "@wordpress/block-editor";
import { PanelBody, SelectControl, RangeControl } from "@wordpress/components";

/**
 * Swift Block Animation Plugin for WordPress Blocks
 *
 */
import "./style.scss";
import "./view.js";

// Swift Block Animation options
const ANIMATION_OPTIONS = [
  { label: "None", value: "" },

  // Fade Animations
  { label: "Fade In", value: "fadeIn" },
  { label: "Fade In Up", value: "fadeInUp" },
  { label: "Fade In Down", value: "fadeInDown" },
  { label: "Fade In Left", value: "fadeInLeft" },
  { label: "Fade In Right", value: "fadeInRight" },
  { label: "Fade In Up Big", value: "fadeInUpBig" },
  { label: "Fade In Down Big", value: "fadeInDownBig" },
  { label: "Fade In Left Big", value: "fadeInLeftBig" },
  { label: "Fade In Right Big", value: "fadeInRightBig" },

  // Slide Animations
  { label: "Slide In Up", value: "slideInUp" },
  { label: "Slide In Down", value: "slideInDown" },
  { label: "Slide In Left", value: "slideInLeft" },
  { label: "Slide In Right", value: "slideInRight" },

  // Zoom Animations
  { label: "Zoom In", value: "zoomIn" },
  { label: "Zoom In Up", value: "zoomInUp" },
  { label: "Zoom In Down", value: "zoomInDown" },
  { label: "Zoom In Left", value: "zoomInLeft" },
  { label: "Zoom In Right", value: "zoomInRight" },

  // Bounce Animations
  { label: "Bounce In", value: "bounceIn" },
  { label: "Bounce In Up", value: "bounceInUp" },
  { label: "Bounce In Down", value: "bounceInDown" },
  { label: "Bounce In Left", value: "bounceInLeft" },
  { label: "Bounce In Right", value: "bounceInRight" },

  // Flip Animations
  { label: "Flip In X", value: "flipInX" },
  { label: "Flip In Y", value: "flipInY" },

  // Light Speed
  { label: "Light Speed In Right", value: "lightSpeedInRight" },
  { label: "Light Speed In Left", value: "lightSpeedInLeft" },

  // Roll
  { label: "Roll In", value: "rollIn" },
];

// Add animation attributes.
function addAnimationAttribute(settings, name) {
  // Only apply to core blocks
  if (!name.startsWith("core/")) return settings;

  settings.attributes = {
    ...settings.attributes,
    // Main animation settings
    swiftAnimation: {
      type: "string",
      default: "",
    },
    swiftAnimationDuration: {
      type: "number",
      default: 600,
    },
    swiftAnimationDelay: {
      type: "number",
      default: 0,
    },
    // Viewport settings
    swiftViewportOffset: {
      type: "number",
      default: 15,
    },
    // Stagger settings for grouped animations
    swiftStagger: {
      type: "boolean",
      default: false,
    },
    swiftStaggerDelay: {
      type: "number",
      default: 100,
    },
    swiftStaggerGroup: {
      type: "string",
      default: "",
    },
  };

  return settings;
}
addFilter(
  "blocks.registerBlockType",
  "swift-style/add-animation-attribute",
  addAnimationAttribute
);

// Add animation controls to Inspector
const withSwiftAnimationControl = createHigherOrderComponent((BlockEdit) => {
  return (props) => {
    const { attributes, setAttributes, name } = props;

    if (!name.startsWith("core/")) return <BlockEdit {...props} />;

    const {
      swiftAnimation,
      swiftAnimationDuration,
      swiftAnimationDelay,
      swiftViewportOffset,
      swiftStagger,
      swiftStaggerDelay,
      swiftStaggerGroup,
    } = attributes;

    return (
      <Fragment>
        <BlockEdit {...props} />
        <InspectorControls>
          <PanelBody
            title={__("Entrance Animation", "swift-block-animation")}
            initialOpen={false}
          >
            <SelectControl
              label={__("Animation", "swift-block-animation")}
              value={swiftAnimation}
              options={ANIMATION_OPTIONS}
              onChange={(value) => setAttributes({ swiftAnimation: value })}
              help={__(
                "Choose entrance animation effect",
                "swift-block-animation"
              )}
            />

            {swiftAnimation && (
              <>
                <RangeControl
                  label={__("Animation Duration (ms)", "swift-block-animation")}
                  value={swiftAnimationDuration}
                  onChange={(value) =>
                    setAttributes({ swiftAnimationDuration: value })
                  }
                  min={100}
                  max={3000}
                  step={50}
                  help={__(
                    "How long the animation takes to complete",
                    "swift-block-animation"
                  )}
                />

                <RangeControl
                  label={__("Animation Delay (ms)", "swift-block-animation")}
                  value={swiftAnimationDelay}
                  onChange={(value) =>
                    setAttributes({ swiftAnimationDelay: value })
                  }
                  min={0}
                  max={3000}
                  step={50}
                  help={__(
                    "Delay before animation starts",
                    "swift-block-animation"
                  )}
                />

                <RangeControl
                  label={__("Viewport Offset (%)", "swift-block-animation")}
                  value={swiftViewportOffset}
                  onChange={(value) =>
                    setAttributes({ swiftViewportOffset: value })
                  }
                  min={0}
                  max={100}
                  step={5}
                  help={__(
                    "Trigger when element is X% visible in viewport",
                    "swift-block-animation"
                  )}
                />

                <hr />

                <label>
                  <input
                    type="checkbox"
                    checked={swiftStagger}
                    onChange={(e) =>
                      setAttributes({ swiftStagger: e.target.checked })
                    }
                  />{" "}
                  {__("Enable Stagger Animation", "swift-block-animation")}
                </label>

                {swiftStagger && (
                  <>
                    <RangeControl
                      label={__("Stagger Delay (ms)", "swift-block-animation")}
                      value={swiftStaggerDelay}
                      onChange={(value) =>
                        setAttributes({ swiftStaggerDelay: value })
                      }
                      min={0}
                      max={1000}
                      step={50}
                      help={__(
                        "Delay between each element in group",
                        "swift-block-animation"
                      )}
                    />

                    <SelectControl
                      label={__("Stagger Group", "swift-block-animation")}
                      value={swiftStaggerGroup}
                      options={[
                        {
                          label: __(
                            "Auto (same parent)",
                            "swift-block-animation"
                          ),
                          value: "",
                        },
                        {
                          label: __("Group A", "swift-block-animation"),
                          value: "group-a",
                        },
                        {
                          label: __("Group B", "swift-block-animation"),
                          value: "group-b",
                        },
                        {
                          label: __("Group C", "swift-block-animation"),
                          value: "group-c",
                        },
                        {
                          label: __("Group D", "swift-block-animation"),
                          value: "group-d",
                        },
                      ]}
                      onChange={(value) =>
                        setAttributes({ swiftStaggerGroup: value })
                      }
                      help={__(
                        "Group elements for sequential animation",
                        "swift-block-animation"
                      )}
                    />
                  </>
                )}
              </>
            )}
          </PanelBody>
        </InspectorControls>
      </Fragment>
    );
  };
}, "withSwiftAnimationControl");

addFilter(
  "editor.BlockEdit",
  "swift-style/with-animation-control",
  withSwiftAnimationControl
);

// Apply animation attributes to save output
function applySwiftAnimationProps(extraProps, blockType, attributes) {
  if (!blockType.name.startsWith("core/")) return extraProps;

  const {
    swiftAnimation,
    swiftAnimationDuration,
    swiftAnimationDelay,
    swiftViewportOffset,
    swiftStagger,
    swiftStaggerDelay,
    swiftStaggerGroup,
  } = attributes;

  if (swiftAnimation) {
    // Add data attributes
    extraProps["data-swift-animation"] = swiftAnimation;
    extraProps["data-swift-animation-duration"] =
      swiftAnimationDuration.toString();

    if (swiftAnimationDelay > 0) {
      extraProps["data-swift-animation-delay"] = swiftAnimationDelay.toString();
    }

    if (swiftViewportOffset !== 15) {
      extraProps["data-swift-viewport-offset"] = swiftViewportOffset.toString();
    }

    if (swiftStagger) {
      extraProps["data-swift-stagger"] = "true";
      extraProps["data-swift-stagger-delay"] = swiftStaggerDelay.toString();

      if (swiftStaggerGroup) {
        extraProps["data-swift-stagger-group"] = swiftStaggerGroup;
      }
    }

    // Add CSS classes for immediate styling (hidden initially)
    extraProps.className = (extraProps.className || "") + " swift-invisible";
  }

  return extraProps;
}
addFilter(
  "blocks.getSaveContent.extraProps",
  "swift-style/apply-animation-props",
  applySwiftAnimationProps
);
